.org-detail-container{
    display: flex;
    flex-direction: column;
    margin-top: 16px;
}

.project-description{
    margin-top: 16px;
    white-space: pre-line;
}

.proposal-container{
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;
    color: #2185d0;
}

.proposal-container-mobile{
    display: flex;
    justify-content: center;
    margin-top: 15px;
    color: #2185d0;
}

.proposal-container .ui.blue.button.view-proposal-button{
    margin-top: 1rem;
    box-shadow: 0 0 0 1px #293b52 inset !important;
    background-color: #293b52 !important;
  }

  .mobile-org-picture{
      width: 100%;
      margin-bottom: 5px;
  }
