.app-header {
  background: #293b52 !important; 
  color: #f8d26c;
}

.app-header-logo{
  vertical-align: middle;
  margin: 0 4px;
}

.main-container{
  padding: 0px 4px;
  overflow: hidden;
  box-sizing: border-box;
}

.scrollable-menu {
  height: calc(100vh - 84px);
  margin-top: 14px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 5px;
  border: none;
  padding-right: 1em;
}
